import { Page } from 'pages/Share.styles';

import backBig from 'assets/images/notebook-back-big.png';
import frontBig from 'assets/images/notebook-front-big.png';
import leftBig from 'assets/images/notebook-left-big.png';
import rightBig from 'assets/images/notebook-right-big.png';
import { useParams } from 'react-router-dom';
import { useEffect } from 'react';
import { getProductById, products } from 'utils/mock/products';
import Product from 'pages/Product/screens/Product/Product';

const images = [
	{ src: backBig, alt: 'thumbnail' },
	{ src: frontBig, alt: 'thumbnail' },
	{ src: leftBig, alt: 'thumbnail' },
	{ src: rightBig, alt: 'thumbnail' },
];

const ProductPage = () => {
	const { productId } = useParams();

	if (!productId) return <></>;
	// const { description, title } = getProductById(productId ? +productId : 0);

	const { description, title } = products[Math.max(+productId - 1, 0)];

	return (
		<Page>
			<Product title={title} description={description} images={images} />
		</Page>
	);
};

export default ProductPage;
